*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

body{
  font-family: Helvetica,Arial, sans-serif;
  line-height:1.4;
}

a{
  text-decoration: none;
  color:#ccc;
}

.container{
  padding: 0 1rem;
}

.btn{
  display:inline-block;
  border:none;
  background:#555;
  color: #fff;
  padding:7px 20px;
  cursor:pointer;
}
.btn:hover{
  background:#666;
}
